@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
	--clr-bg-800: #303030;
	--clr-bg-600: #202020;
	--clr-bg-400: #181818;

	--clr-bg-400-r: 24;
	--clr-bg-400-g: 24;
	--clr-bg-400-b: 24;
	--clr-bg-400: rgb(var(--clr-bg-400-r), var(--clr-bg-400-g), var(--clr-bg-400-b));



	--clr-fg-800-r: 196;
	--clr-fg-800-g: 196;
	--clr-fg-800-b: 196;
	--clr-fg-800: rgb(var(--clr-fg-800-r), var(--clr-fg-800-g), var(--clr-fg-800-b));

	--header-height: 98px;
	--footer-height: 48px;

	--padding: 35px;
	--padding-above: 20px;

	--font-size: 14px;

	font-size: 14px;

	font-size: var(--font-size);

	overflow-x: hidden;
}

.page {
	padding: calc(98px + 20px) 0 48px 0;
	padding: calc(var(--header-height) + var(--padding-above)) 0 var(--footer-height) 0;

	min-height: 100vh;

	background-color: rgb(24, 24, 24);

	background-color: var(--clr-bg-400);

	color: rgb(196, 196, 196);

	color: var(--clr-fg-800);

	overflow-x: hidden;
}

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}

.container, .page{
	padding-left: 35px;
	padding-left: var(--padding);
	padding-right: 35px;
	padding-right: var(--padding);

	width: 100vw;
}

@media only screen and (min-width: 700px) {

	:root {
		--header-height: 64px;
		--footer-height: 64px;
		--padding: 70px;
		--padding-above: 48px;
		--font-size: 24px;
	}
}

@media only screen and (max-width: 325px) {
	:root {
		--padding: 20px;
	}
}
.navbar {
	background-color: var(--clr-bg-600);
	border-bottom: 1px solid var(--clr-bg-800);

	height: var(--header-height);

	position: fixed;

	-webkit-transform-origin: top;

	        transform-origin: top;

	z-index: 1;
}

.navbar .container {
	position: relative;

	height: var(--header-height);

	-webkit-transform-origin: top;

	        transform-origin: top;
}

.navbar-container {
	--font-size: 40px;

	font-size: var(--font-size);

	line-height: calc(var(--font-size));

	position: absolute;
	top: 50%;

	-webkit-transform: translateY(-50%);

	        transform: translateY(-50%);
}

.navbar-container a {
	text-decoration: underline;
}

.navbar-container :is(a:link, a:visited, a:hover, a:active) {
	text-decoration: none;

	color: var(--clr-fg-800);
}

.logo-split, .logo-split p {
	width: -webkit-max-content;
	width: max-content;
}

#logo-first::before {
	content: "R";
	color: var(--clr-fg-800);
}

#logo-second::before {
	content: "G";
	color: var(--clr-fg-800);
}

@media only screen and (min-width: 700px) {
	.logo-split {
		display: -webkit-flex;
		display: flex;

		-webkit-flex-direction: row;

		        flex-direction: row;
	}

	.navbar-container {
		--font-size: 48px;
	}

	.logo-split :is(p + p) {
		margin-left: calc(var(--font-size)/3);
	}
}
.split {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;

}

.split .card {

	background-image: url(/PersonalSite/static/media/home.5fddd894.jpg);
	background-size: cover;

	height: 11.5rem;
	width: 10rem;

	border-radius: 8px;

	position: relative;
}

.card-content {
	position: absolute;

	bottom: 0;

	padding: 2ch 0 2ch 3ch;
}

.grid-item {
	min-height: 10rem;

	-webkit-transform: scale(1);

	        transform: scale(1);
	-webkit-animation: transform 300ms ease;
	        animation: transform 300ms ease;

	border-radius: 8px;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;

	padding: 0.5ch;
}

a.grid-item:hover, a.grid-item:visited, a.grid-item:link, a.grid-item:active {
	text-decoration: none;
	color: var(--clr-fg-800);
}

.grid-item:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);

	background-color: var(--clr-bg-600);

	cursor: pointer;
}

.grid {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 1.5rem;
	gap: 1.5rem;

	margin: 2.5rem 0 2.5rem 0;
}

.home-button {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.home-button h2 {
	text-align: center;
}

@media only screen and (min-width: 550px) {
	.grid {
		grid-template-columns: repeat(3, 1fr);
	}
}

@media only screen and (min-width: 1250px) {
	.grid {
		grid-template-columns: repeat(6, 1fr);
	}
}

@media only screen and (max-width: 700px) {
	.card {
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		        align-items: center;

		margin-bottom: 2.5rem;
	}

	.card-content {
		position: relative;

		padding: 0 0 0 3ch;

		-webkit-transform: translateX(11.5rem);

		        transform: translateX(11.5rem);
	}
}

@media only screen and (min-width: 700px) {

	.split {

		margin-bottom: 2.5rem;

		display: flow-root;
	}

	.card {
		float: left;

		margin: 0 3ch 0 0;

		overflow: hidden;
	}

	.card+p {
		text-align: justify;
	}

	.card-content {
		width: 100%;
	
		background: linear-gradient(
			rgba(var(--clr-bg-400-r), var(--clr-bg-400-g), var(--clr-bg-400-b), 0%),
			rgba(var(--clr-bg-400-r), var(--clr-bg-400-g), var(--clr-bg-400-b), 25%) 10%,
			rgba(var(--clr-bg-400-r), var(--clr-bg-400-g), var(--clr-bg-400-b), 100%)
		);

		-webkit-transform: translateY(80%);

		        transform: translateY(80%);
		transition: -webkit-transform 300ms ease;
		transition: transform 300ms ease;
		transition: transform 300ms ease, -webkit-transform 300ms ease;
	}

	.card-content > * {
		opacity: 0;
		transition: opacity 300ms linear;
	}

	.card:hover .card-content > * {
		opacity: 1;
	}

	.card:hover .card-content {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}
.footer {
	background-color: var(--clr-bg-600);
	border-top: 1px solid var(--clr-bg-800);

	height: var(--footer-height);

	position: fixed;

	bottom: 0;

	z-index: 1;

	--icon-size: 20px;
	--font-size: 13px;
}

.footer .container {
	position: relative;

	height: var(--footer-height);
}

.footer-container {
	position: absolute;
	top: 50%;

	-webkit-transform: translateY(-50%);

	        transform: translateY(-50%);

	color: var(--clr-fg-800);

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;

	width: calc(100% - 2 * var(--padding));
}

.footer-container p {

	font-size: var(--font-size);
	font-weight: bold;

	line-height: var(--icon-size);

	-webkit-flex-grow: 1;

	        flex-grow: 1;
}

.footer-icons {

	font-size: var(--icon-size);

	height: var(--icon-size);
}

.footer-icons :is(svg+svg) {
	margin-left: 10px;
}

@media only screen and (min-width: 700px) {
	.footer {
		--icon-size: 28px;
		--font-size: 16px;
	}
}

@media only screen and (max-width: 325px) {
	.footer {
		--icon-size: 14px;
		--font-size: 10px;
	}
}
